;\n    }\n}\n\n@mixin border-right-1px($color) {\n    &:before {\n        border-right: 1px solid $color;\n    }\n}\n\n@mixin border-bottom-1px($color) {\n    &:before {\n        border-bottom: 1px solid $color;\n    }\n}\n\n@mixin border-left-1px($color) {\n    &:before {\n        border-left: 1px solid $color;\n    }\n}\n\n@mixin border-1px($color) {\n    position: relative;\n    &:after {\n        content: '';\n        pointer-events: none; //解决iphone上的点击无效Bug\n        position: absolute;\n        left: 0;\n        top: 0;\n        transform-origin: 0 0;\n        border: 1px solid $color;\n    }\n}\n\n//圆角\n@mixin borderRadius($radius) {\n    -webkit-border-radius: $radius;\n    -moz-border-radius: $radius;\n    -ms-border-radius: $radius;\n    -o-border-radius: $radius;\n    border-radius: $radius;\n}\n\n//1px底部边框\n@mixin border-1px($color) {\n    position: relative;\n    &:after {\n        display: block;\n        position: absolute;\n        left: 0;\n        bottom: 0;\n        width: 100%;\n        border-top: 1px solid $color;\n        content: '';\n    }\n}\n\n//定位全屏\n@mixin allcover {\n    position: absolute;\n    top: 0;\n    right: 0;\n}\n\n//定位上下左右居中\n@mixin center {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n}\n\n//定位上下居中\n@mixin ct {\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n}\n\n//定位左右居中\n@mixin cl {\n    position: absolute;\n    left: 50%;\n    transform: translateX(-50%);\n}\n\n//宽高\n@mixin wh($width, $height) {\n    width: $width;\n    height: $height;\n}\n\n//字体大小、行高、字体\n@mixin font($size, $line-height, $family: 'Microsoft YaHei') {\n    font: #{$size}/#{$line-height} $family;\n}\n\n//字体大小，颜色\n@mixin sc($size, $color) {\n    font-size: $size;\n    color: $color;\n}\n\n@mixin boxSizing {\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box;\n}\n\n//flex 布局和 子元素 对其方式\n@mixin fj($type: space-between) {\n    display: flex;\n    justify-content: $type;\n}\n\n@mixin ellipsis1 {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n@mixin ellipsis2($num) {\n    display: -webkit-box;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    word-wrap: break-word;\n    white-space: normal !important;\n    -webkit-line-clamp: $num;\n    /*! autoprefixer: off */\n    -webkit-box-orient: vertical;\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"../../common/style/mixin.scss\";\n.faceLoginWrap {\n  display: flex;\n  flex-direction: column;\n  background: rgba(255, 255, 255, 0.85);\n\n  .title {\n    padding-bottom: 10px;\n    height: 20%;\n    font-size: 28px;\n    color: $titleColor;\n    box-sizing: border-box;\n  }\n\n  .faceScan {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    padding-bottom: 20px;\n    .container {\n      width: 100%;\n      display: flex;\n      flex-direction: column;\n      justify-content: center;\n      #video {\n        align-self: center;\n        background: url(\"../../assets/images/qr-bg.jpg\");\n      }\n      #canvas {\n        align-self: center;\n        margin-top: -166px;\n      }\n    }\n\n  }\n  .fooer {\n    font-size: 14px;\n    text-align: center;\n    line-height: 14px;\n    color: $textColor;\n    .footer-btn {\n      cursor: pointer;\n    }\n    .midLine {\n      height: 14px;\n      line-height: 14px;\n      padding: 0 8px;\n    }\n  }\n}\n"]}]);




